.container {
    width: 100%;

    display: flex;
}

.box {
    width: 20%;

    margin: 0 5px 0 5px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.input {
    width: 100%;
    margin-top: 10px;

    font-size: 18px;
    text-align: center;

    background: none;
    border-bottom: 2px solid black;
}

.input::-webkit-outer-spin-button,
.input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}