.contactsTable td {
    border: 2px solid black;
    padding: 5px;
    text-align: center;
    font-size: 15px;
    cursor: help;
}

.contactsTable td:hover {
    background: rgba(242, 246, 0, 0.5);
}

.contactsTable td:active {
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
}